font-weight: 400;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
margin: 8px 0 32px;
color: #949494;
text-align: center;
}
.column {
float: left;
width: 49%;
}

#input-form{
margin: 20px;
margin-top: 65px;

}
.row:after {
content: "";
display: table;
clear: both;
}
#left-Column{
border-right: 1px solid #ddd;
}
form{
display: block;
padding-left: 32px;
}
.login-title{
text-align: left;
color: #333;
font-weight: bold;
margin-bottom: 8px;
font-size: 13px;
}
input{
width: 100%;
font-size: 15px;
line-height: 1.4;
padding-left: 8px;
padding-right: 8px;
min-height: 32px;
margin-bottom: 8px;
background-color: #EEE;
padding: 3px 5px;
height: 15px;
border: 1px solid #e2e2e2;
border-radius: 3px;
box-shadow: inset 0 1px 1px rgba(200,200,200,0.3);
