* {box-sizing: border-box;}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
#fullbody{
    margin: 0px;
    padding: 0px;
}
.topnav {
    overflow: hidden;
    background-color: #fff;
    height: 50px;
  }
  
  .topnav img{
    float: left;
    display: block;
    width: 202px;
    height: 30px;
    margin-top: 10px;
  }
  .topnav a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  .topnav button {
    float: left;
    display: block;
    margin-top:6px;
  }
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #fff;
    color: #990000;
  border-bottom: 2.5px solid #990000;
  padding-bottom: 12px;
    font-weight: 500;
    font-size: 18px;
  }
  
  .topnav .search-container {
    float: left;
  }
  
  .topnav input[type=text] {
    font-family: FontAwesome;
    padding: 6px;
    margin-top: 8px;
    font-size: 17px;
      border: 1px solid #ddd;
      border-radius: 5px;
  }
  
  .topnav .search-container button {
    float: right;
    padding: 6px 10px;
    margin-top: 8px;
    margin-left: 10px;
    background: #ddd;
    font-size: 17px;
    border: none;
    cursor: pointer;
  }
  
  .topnav .search-container button:hover {
    background: #ccc;
  }
  
  @media screen and (max-width: 600px) {
    .topnav .search-container {
      float: none;
    }
    .topnav a, .topnav input[type=text], .topnav .search-container button {
      float: none;
      display: block;
      text-align: left;
      width: 100%;
      margin: 0;
      padding: 14px;
    }
    .topnav input[type=text] {
      border: 1px solid #ccc;
    }
  }
  #innerNav{
    margin-left: 20%;
  }
  .add_Button{
    background-color: rgb(189,43,39);
   border: none;
   color: white;
   padding: 10px 25px;
   text-align: center;
   text-decoration: none;
   display: inline-block;
   font-size: 16px;
   margin: 4px 2px;
   cursor: pointer;
   border-radius: 25px;
  }
  #content{
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
  }
  
  .demo:empty {
      margin: auto;
          width: 500px;
          height: 600px;
  
          background-image:
              radial-gradient( circle 50px at 50px 50px, lightgray 99%, transparent 0 ),
              linear-gradient( 100deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.5) 50%, rgba(255, 255, 255, 0) 80% ),
              linear-gradient( lightgray 20px, transparent 0 ),
              linear-gradient( lightgray 20px, transparent 0 ),
              linear-gradient( lightgray 20px, transparent 0 ),
              linear-gradient( lightgray 20px, transparent 0 );
  
          background-repeat: repeat-y;
  
          background-size:
              100px 200px,
              50px 200px,
              150px 200px,
              350px 200px,
              300px 200px,
              250px 200px;
  
          background-position:
              0 0,
              0 0,
              120px 0,
              120px 40px,
              120px 80px,
              120px 120px;
  
          -webkit-animation: shine 1s infinite;
  
                  animation: shine 1s infinite;
      }
  
      @-webkit-keyframes shine {
          to {
              background-position:
                  0 0,
                  100% 0, /* move highlight to right */
                  120px 0,
                  120px 40px,
                  120px 80px,
                  120px 120px;
          }
      }
  
      @keyframes shine {
          to {
              background-position:
                  0 0,
                  100% 0, /* move highlight to right */
                  120px 0,
                  120px 40px,
                  120px 80px,
                  120px 120px;
          }
      }
